@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-background text-white font-sans;
  }
}

@layer components {
  .btn {
    @apply px-4 py-2 rounded-md transition-all duration-200 ease-in-out;
  }

  .btn-primary {
    @apply bg-primary text-black hover:bg-primary/90;
  }

  .btn-secondary {
    @apply bg-surface border border-border hover:bg-secondary;
  }

  .input {
    @apply bg-surface border border-border text-white px-3 py-2 rounded-md focus:outline-none focus:border-primary;
  }

  .card {
    @apply bg-surface border border-border rounded-lg p-4;
  }

  .dropdown-menu {
    @apply absolute top-full left-0 mt-2 bg-surface border border-border rounded-md shadow-lg min-w-[200px] z-50;
  }

  .dropdown-item {
    @apply flex items-center gap-2 w-full px-4 py-2 text-left hover:bg-secondary transition-colors duration-200;
  }
}

/* Material Icons specific styles */
.material-icons {
  font-size: 20px !important;
}

/* Image handling styles */
.image-placeholder {
  @apply flex items-center justify-center bg-surface rounded-lg transition-all duration-200;
}

.image-placeholder.large {
  @apply w-full h-[420px] cursor-pointer hover:bg-secondary;
}

.image-overlay {
  @apply absolute inset-0 bg-black/70 flex items-center justify-center opacity-0 transition-opacity duration-300;
}

/* Base styles */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #000;
  color: #fff;
}

.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

/* Header styles */
.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 1.5rem;
  background-color: #111;
  border-bottom: 1px solid #333;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.lock-icon {
  color: #00cc66;
  font-size: 20px !important;
}

.app-title {
  font-size: 1.25rem;
  font-weight: 500;
  text-transform: uppercase;
  color: #fff;
  user-select: none;
}

.dropdown-button {
  background: none;
  border: none;
  color: #fff;
  padding: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.dropdown-button .material-icons {
  font-size: 20px;
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-left: 1.5rem;
}

.header-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: #222;
  border: 1px solid #333;
  color: #fff;
  padding: 0.5rem 0.75rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s ease;
}

.header-button .material-icons {
  font-size: 20px;
}

.header-button:hover {
  background-color: #333;
  border-color: #444;
}

.header-button.primary {
  background-color: #00cc66;
  border-color: #00cc66;
  color: #000;
}

.header-button.primary:hover {
  background-color: #00b359;
  border-color: #00b359;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.icon-button {
  background: none;
  border: none;
  color: #fff;
  padding: 0.5rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: all 0.2s ease;
}

.icon-button:hover {
  background-color: #222;
}

.icon-button .material-icons {
  font-size: 20px;
}

/* CSV Importer styles */
.csv-importer {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border-radius: 0;
}

.csv-importer h2 {
  display: none; /* Hide the title since it's now in the modal header */
}

.drop-area {
  border: 2px dashed #555;
  border-radius: 4px;
  padding: 2rem;
  text-align: center;
  transition: all 0.3s ease;
  background-color: #1a1a1a;
  position: relative;
}

.drop-area.active {
  border-color: #00cc66;
  background-color: rgba(0, 204, 102, 0.1);
}

.drop-area.loading {
  opacity: 0.7;
  cursor: not-allowed;
}

.drop-area input[type="file"] {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 2;
}

.drop-area label {
  color: #aaa;
  font-size: 1.1rem;
  cursor: pointer;
}

.drop-area label.disabled {
  cursor: not-allowed;
}

.error-message {
  margin-top: 1rem;
  padding: 0.75rem;
  background-color: rgba(255, 0, 0, 0.1);
  border-left: 4px solid #ff3333;
  color: #ff9999;
}

.import-instructions {
  margin-top: 2rem;
  color: #aaa;
}

.import-instructions h3 {
  margin-bottom: 0.5rem;
  color: #fff;
}

.import-instructions ul {
  margin: 0.5rem 0 0.5rem 1.5rem;
}

.import-instructions li {
  margin-bottom: 0.25rem;
}

/* Card List styles */
.card-list-container {
  margin-top: 2rem;
}

.card-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.search-container {
  flex: 1;
}

.search-input {
  width: 100%;
  padding: 0.75rem;
  border-radius: 4px;
  border: none;
  background-color: #111;
  color: #fff;
  font-size: 1rem;
}

.card-list-controls {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-left: auto;
}

.sort-dropdown {
  position: relative;
}

.sort-button {
  background: none;
  border: none;
  color: #fff;
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.sort-button:hover {
  color: #00cc66;
}

.sort-dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #111;
  border: 1px solid #333;
  border-radius: 4px;
  min-width: 160px;
  z-index: 100;
  margin-top: 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.sort-dropdown-item {
  display: block;
  width: 100%;
  padding: 8px 16px;
  text-align: left;
  background: none;
  border: none;
  color: #aaa;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s ease;
}

.sort-dropdown-item:hover {
  background-color: #222;
  color: #fff;
}

.sort-dropdown-item.active {
  color: #00cc66;
  background-color: rgba(0, 204, 102, 0.1);
}

.sort-direction-button {
  background: none;
  border: none;
  color: #fff;
  font-size: 1.2rem;
  padding: 0.25rem 0.5rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 32px;
  transition: all 0.2s ease;
}

.sort-direction-button:hover {
  color: #00cc66;
  transform: scale(1.1);
}

.results-count {
  margin: 1rem 0;
  font-size: 1.2rem;
  font-weight: bold;
}

.card-list {
  display: flex;
  flex-direction: column;
}

.card-item {
  display: flex;
  align-items: center;
  padding: 1.5rem 1rem;
  border-bottom: 1px solid #333;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.card-item:hover {
  background-color: #111;
}

.card-checkbox {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1rem;
  z-index: 1;
}

.card-checkbox input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
}

/* List view thumbnail styles */
.card-list .card-image {
  width: 60px;
  height: 85px;
  margin-right: 1rem;
  border-radius: 4px;
  overflow: hidden;
  background-color: #1a1a1a;
}

.card-list .card-thumbnail {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background-color: #1a1a1a;
  transition: transform 0.2s ease;
}

.card-item:hover .card-thumbnail {
  transform: scale(1.05);
}

.image-placeholder {
  width: 100%;
  height: 100%;
  background-color: #1a1a1a;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-info {
  flex: 1;
}

.card-info h3 {
  font-size: 1rem;
  font-weight: normal;
  color: #aaa;
}

.card-info h2 {
  font-size: 1.5rem;
  margin: 0.25rem 0 0.5rem;
}

.card-tags {
  display: flex;
  gap: 0.5rem;
}

.tag {
  display: inline-block;
  padding: 0.25rem 0.75rem;
  border-radius: 50px;
  font-size: 0.8rem;
  background-color: #333;
}

.card-profit {
  margin-left: 2rem;
  text-align: right;
  min-width: 120px;
}

.profit-label {
  color: #aaa;
  font-size: 0.8rem;
  text-align: right;
  margin-bottom: 0.25rem;
}

.profit-value {
  font-size: 1.1rem;
  font-weight: bold;
  text-align: right;
}

.profit-value.positive {
  color: #00cc66;
}

.profit-value.negative {
  color: #ff3333;
}

.card-list-summary {
  display: flex;
  justify-content: space-between;
  padding: 1.5rem;
  margin-bottom: 2rem;
  background-color: #111;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.summary-item {
  text-align: center;
  flex: 1;
  padding: 0 1rem;
  border-right: 1px solid #333;
}

.summary-item:last-child {
  border-right: none;
}

.summary-label {
  font-size: 0.9rem;
  color: #888;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.summary-value {
  font-size: 1.8rem;
  font-weight: bold;
  color: #fff;
}

.summary-value.positive {
  color: #00cc66;
}

.summary-value.negative {
  color: #ff3333;
}

/* Card Details styles */
.card-details {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  z-index: 1000;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.card-details-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  background-color: #111;
  border-bottom: 1px solid #333;
  position: sticky;
  top: 0;
  z-index: 10;
}

.close-button {
  background: none;
  border: none;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0.5rem;
  line-height: 1;
  display: flex;
  align-items: center;
}

.action-buttons {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.action-button {
  background: none;
  border: none;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0.5rem;
  display: flex;
  align-items: center;
}

.card-details-content {
  max-width: 900px;
  margin: 0 auto;
  padding: 2rem;
  width: 100%;
  flex: 1;
}

.card-details-main {
  display: flex;
  gap: 2rem;
  margin-bottom: 3rem;
}

.card-details-image {
  flex: 0 0 300px;
}

.image-placeholder.large {
  width: 100%;
  height: 420px;
  background-color: #333;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.card-details-info {
  flex: 1;
}

.card-details-info h1 {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.card-value {
  margin-bottom: 2rem;
}

.value-label {
  font-size: 1rem;
  color: #aaa;
  margin-bottom: 0.5rem;
}

.value {
  font-size: 2rem;
  font-weight: bold;
}

.card-meta {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.meta-item {
  display: flex;
  flex-direction: column;
}

.meta-label {
  font-size: 0.9rem;
  color: #aaa;
}

.meta-value {
  font-size: 1.2rem;
  font-weight: bold;
}

.card-details-investment {
  margin-bottom: 3rem;
}

.card-details-investment h2, 
.card-details-notes h2 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  color: #aaa;
}

.investment-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

.investment-label {
  font-size: 0.9rem;
  color: #aaa;
  margin-bottom: 0.5rem;
}

.investment-value {
  font-size: 1.5rem;
  font-weight: bold;
}

.investment-value.positive {
  color: #00cc66;
}

.investment-value.negative {
  color: #ff3333;
}

.clickable-value {
  cursor: pointer;
  display: inline-block;
}

.clickable-value:hover {
  text-decoration: underline;
}

.investment-input {
  background-color: #222;
  border: 1px solid #444;
  color: #fff;
  padding: 0.5rem;
  font-size: 1.2rem;
  width: 150px;
  margin-right: 0.5rem;
}

.save-button {
  background-color: #00cc66;
  color: #000;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.2s ease;
}

.save-button:hover {
  background-color: #00b359;
}

.save-button:disabled {
  background-color: #666;
  cursor: not-allowed;
  opacity: 0.7;
}

.notes-textarea {
  width: 100%;
  height: 150px;
  background-color: #222;
  border: 1px solid #444;
  color: #fff;
  padding: 1rem;
  font-size: 1rem;
  resize: vertical;
  border-radius: 4px;
}

.importer-toggle {
  display: flex;
  margin-bottom: 1rem;
  border-radius: 4px;
  overflow: hidden;
  width: fit-content;
}

.toggle-button {
  padding: 0.75rem 1.5rem;
  background-color: #222;
  border: none;
  color: #aaa;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.toggle-button:hover {
  background-color: #333;
}

.toggle-button.active {
  background-color: #00cc66;
  color: #000;
  font-weight: bold;
}

.header-left {
  display: flex;
  align-items: center;
}

.dropdown-container {
  position: relative;
  display: flex;
  align-items: center;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 1.5rem;
  margin-top: 0.5rem;
  background-color: #111;
  border: 1px solid #333;
  border-radius: 4px;
  min-width: 200px;
  z-index: 1000;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

.dropdown-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  width: 100%;
  padding: 0.75rem 1rem;
  text-align: left;
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s ease;
}

.dropdown-item:hover {
  background-color: #222;
}

.dropdown-item.add-collection {
  border-top: 1px solid #333;
  color: #00cc66;
}

.dropdown-item.add-collection:hover {
  background-color: rgba(0, 204, 102, 0.1);
}

.dropdown-item .material-icons {
  font-size: 18px;
}

.dropdown-button {
  background: none;
  border: none;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
  padding: 4px 8px;
  transition: transform 0.2s ease;
}

.dropdown-button:hover {
  transform: translateY(2px);
}

/* Settings Modal styles */
.settings-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
}

.settings-content {
  background-color: #111;
  border-radius: 8px;
  width: 100%;
  max-width: 500px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

.settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid #333;
}

.settings-header h2 {
  font-size: 1.5rem;
  margin: 0;
}

.settings-body {
  padding: 1.5rem;
}

.settings-section {
  margin-bottom: 2rem;
}

.settings-section:last-child {
  margin-bottom: 0;
}

.settings-section h3 {
  font-size: 1.2rem;
  color: #aaa;
  margin-bottom: 1rem;
}

.settings-option {
  margin-bottom: 1rem;
}

.settings-option label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
}

.settings-option input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
}

.currency-select {
  margin-left: 1rem;
  background-color: #222;
  border: 1px solid #444;
  color: #fff;
  padding: 0.5rem;
  border-radius: 4px;
  cursor: pointer;
}

.settings-footer {
  padding: 1.5rem;
  border-top: 1px solid #333;
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

.close-button {
  background: none;
  border: none;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0.5rem;
  line-height: 1;
}

.close-button:hover {
  color: #aaa;
}

/* Collection Rename styles */
.collection-rename {
  margin-bottom: 1.5rem;
}

.collection-rename label {
  display: block;
  margin-bottom: 0.5rem;
  color: #aaa;
}

.rename-input-group {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.rename-input {
  flex: 1;
  background-color: #222;
  border: 1px solid #444;
  color: #fff;
  padding: 0.5rem;
  border-radius: 4px;
  font-size: 1rem;
}

.rename-input:focus {
  outline: none;
  border-color: #00cc66;
}

.collection-name-display {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.collection-name-display span {
  font-size: 1.1rem;
  color: #fff;
}

.rename-button {
  background: none;
  border: 1px solid #444;
  color: #fff;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s ease;
}

.rename-button:hover {
  background-color: #222;
}

.rename-button.save {
  background-color: #00cc66;
  border-color: #00cc66;
  color: #000;
}

.rename-button.save:hover {
  background-color: #00b359;
}

.rename-button.cancel {
  background-color: transparent;
  border-color: #666;
}

.rename-button.cancel:hover {
  background-color: #333;
}

/* Import Modal styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: #111;
  border-radius: 8px;
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid #333;
}

.modal-header h2 {
  margin: 0;
  font-size: 1.5rem;
}

.modal-body {
  padding: 1.5rem;
}

/* List actions styles */
.list-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding: 0.5rem 0;
}

.select-all-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.select-all-container input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
}

.select-all-container label {
  font-size: 0.9rem;
  color: #aaa;
  cursor: pointer;
}

.delete-selected-button {
  background-color: #ff3333;
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.delete-selected-button:hover {
  background-color: #ff0000;
  transform: translateY(-1px);
}

/* Card Details Edit Menu */
.edit-menu-container {
  position: relative;
  display: inline-block;
}

.edit-menu {
  position: absolute;
  top: 100%;
  right: 0;
  background: white;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  min-width: 150px;
}

.edit-menu button {
  display: block;
  width: 100%;
  padding: 8px 12px;
  text-align: left;
  border: none;
  background: none;
  cursor: pointer;
  font-size: 14px;
  color: #333;
}

.edit-menu button:hover {
  background-color: #f5f5f5;
}

.card-details-info input {
  font-size: inherit;
  padding: 4px 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin: -4px 0;
}

.card-details-info input:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

/* Action Buttons */
.action-button {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  padding: 4px 8px;
  margin-left: 8px;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.action-button:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

/* Add New Card Form styles */
.add-new-card {
  background-color: #111;
  padding: 2rem;
  border-radius: 8px;
  margin-top: 2rem;
}

.add-new-card h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: #fff;
}

.form-group {
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
}

.form-group label {
  color: #aaa;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}

.form-group input[type="text"],
.form-group input[type="number"],
.form-group input[type="date"] {
  background-color: #222;
  border: 1px solid #444;
  color: #fff;
  padding: 0.75rem;
  border-radius: 4px;
  font-size: 1rem;
}

.form-group input[type="text"]:focus,
.form-group input[type="number"]:focus,
.form-group input[type="date"]:focus {
  outline: none;
  border-color: #00cc66;
}

.form-group input::placeholder {
  color: #666;
}

.form-actions {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
}

.form-actions button {
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.form-actions button[type="submit"] {
  background-color: #00cc66;
  color: #000;
  border: none;
}

.form-actions button[type="submit"]:hover {
  background-color: #00b359;
}

.form-actions button[type="button"] {
  background-color: transparent;
  color: #fff;
  border: 1px solid #444;
}

.form-actions button[type="button"]:hover {
  background-color: #222;
}

/* New Card Form Modal styles */
.new-card-form-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 2rem;
  z-index: 1000;
  overflow-y: auto;
}

.new-card-form {
  background-color: #111;
  border-radius: 8px;
  width: 100%;
  max-width: 900px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

.form-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid #333;
}

.form-header h2 {
  margin: 0;
  font-size: 1.5rem;
  color: #fff;
}

.form-header .close-button {
  background: none;
  border: none;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0.5rem;
  line-height: 1;
}

.form-header .close-button:hover {
  color: #aaa;
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
  padding: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  color: #aaa;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
}

.form-group label::after {
  content: "*";
  color: #ff3333;
  margin-left: 0.25rem;
  display: none;
}

.form-group label[for="player"]::after,
.form-group label[for="slabSerial"]::after {
  display: inline;
}

.form-group input {
  background-color: #222;
  border: 1px solid #444;
  color: #fff;
  padding: 0.75rem;
  border-radius: 4px;
  font-size: 1rem;
  width: 100%;
}

.form-group input:focus {
  outline: none;
  border-color: #00cc66;
  box-shadow: 0 0 0 2px rgba(0, 204, 102, 0.1);
}

.form-group input::placeholder {
  color: #666;
}

.form-group input[type="date"] {
  color-scheme: dark;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 1.5rem;
  border-top: 1px solid #333;
}

.cancel-button {
  background: none;
  border: 1px solid #444;
  color: #fff;
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.2s ease;
}

.cancel-button:hover {
  background-color: #222;
}

.submit-button {
  background-color: #00cc66;
  color: #000;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold;
  transition: all 0.2s ease;
}

.submit-button:hover {
  background-color: #00b359;
}

.submit-button:disabled {
  background-color: #666;
  cursor: not-allowed;
  opacity: 0.7;
}

@media (max-width: 768px) {
  .form-grid {
    grid-template-columns: 1fr;
  }
  
  .new-card-form-overlay {
    padding: 1rem;
  }
}

/* Metric Dropdown styles */
.metric-dropdown {
  position: relative;
  margin-left: 8px;
}

.metric-button {
  background: none;
  border: none;
  color: #fff;
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.metric-button:hover {
  color: #00cc66;
}

/* Image upload styles */
.image-upload {
  grid-column: span 2;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.image-upload input[type="file"] {
  padding: 0.5rem;
  background: #2a2a2a;
  border: 1px solid #444;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
}

.image-upload input[type="file"]::-webkit-file-upload-button {
  background: #444;
  border: none;
  border-radius: 4px;
  color: #fff;
  padding: 0.5rem 1rem;
  margin-right: 1rem;
  cursor: pointer;
}

.image-upload input[type="file"]::-webkit-file-upload-button:hover {
  background: #555;
}

.image-preview {
  width: 100%;
  max-width: 300px;
  height: 200px;
  border-radius: 8px;
  overflow: hidden;
  background: #2a2a2a;
  display: flex;
  align-items: center;
  justify-content: center;
}

.image-preview img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

/* Card Details Image Upload styles */
.card-image-container {
  position: relative;
  width: 100%;
  height: 420px;
  border-radius: 8px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s ease;
}

.card-image-container:hover .image-overlay {
  opacity: 1;
}

/* Modal view image styles */
.card-details .card-image {
  width: 100%;
  height: 420px;
  object-fit: contain;
  background-color: #1a1a1a;
}

.card-details .card-thumbnail {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background-color: #1a1a1a;
}

.image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.image-overlay span {
  color: #fff;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  background: rgba(0, 0, 0, 0.5);
}

.image-placeholder.large {
  width: 100%;
  height: 420px;
  background-color: #1a1a1a;
  border-radius: 8px;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
}

.image-placeholder.large:hover {
  background-color: #222;
}

.image-placeholder.large span {
  color: #666;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  background: rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.image-placeholder.large:hover span {
  color: #fff;
  background: rgba(0, 0, 0, 0.4);
}

/* Remove old import buttons styles */
.import-buttons {
  display: none;
}

.import-button {
  display: none;
}

.collection-selector {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  transition: all 0.2s ease;
}

.collection-selector:hover {
  background-color: #222;
}